.articles-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.articles-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
}

.articles-main-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0 auto;
	padding-top: 80px;
	max-width: 900px;
	position: relative;
	padding-left: 20px;
	padding-right: 20px;
}

.articles-main-container::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: 250px;
	background: linear-gradient(180deg, rgba(231, 225, 215, 0.4) 0%, rgba(249, 246, 242, 0) 100%);
	z-index: -1;
	border-radius: 0 0 30% 30% / 40px;
}

.articles-container {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.articles-wrapper {
	padding-top: 10px;
	padding-bottom: 50px;
	display: flex;
	flex-direction: column;
	gap: 0;
}

.articles-article {
	padding: 20px 0 20px 35px;
	border-left: 2px solid #d5cec4;
	width: 100%;
	background: none;
	border-radius: 0;
	margin-bottom: 0;
	position: relative;
	transition: all 0.3s ease;
}

.articles-article::before {
	content: '';
	position: absolute;
	left: -7px;
	top: 30px;
	width: 12px;
	height: 12px;
	border-radius: 50%;
	background: #e9e3db;
	border: 2px solid #b5a99a;
	z-index: 2;
	transition: all 0.3s ease;
}

.articles-article:hover::before {
	background: #b5a99a;
	transform: scale(1.2);
}

.articles-article:hover {
	background-color: rgba(245, 242, 237, 0.5);
}

.articles-article:not(:last-child)::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	right: 0;
	bottom: -10px;
	height: 1px;
	background: linear-gradient(90deg, transparent, #e4ded7, transparent);
	margin-left: 35px;
}

.articles-title {
	font-size: 2.7rem;
	font-weight: 700;
	margin-bottom: 1rem;
	color: #4a4741;
	letter-spacing: -1px;
	text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.7);
	position: relative;
	display: inline-block;
	padding-bottom: 10px;
}

.articles-title::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100px;
	height: 3px;
	background: linear-gradient(90deg, #b5a99a, transparent);
	border-radius: 3px;
}

.articles-subtitle {
	font-size: 1.25rem;
	color: #6e6b64;
	margin-bottom: 2.5rem;
	max-width: 800px;
	line-height: 1.6;
	font-weight: 500;
	text-shadow: 0.5px 0.5px 0.5px rgba(255, 255, 255, 0.5);
}

/* Mobile First Responsive Design */
@media (max-width: 768px) {
	.articles-main-container {
		padding-top: 80px;
		padding-left: 20px;
		padding-right: 20px;
		max-width: 100%;
		box-sizing: border-box;
		min-height: 100vh;
	}

	.articles-main-container::before {
		height: 180px;
		margin-left: -20px;
		margin-right: -20px;
		border-radius: 0 0 20% 20% / 30px;
	}

	.articles-container {
		width: 100%;
		max-width: 100%;
		box-sizing: border-box;
	}

	.articles-title {
		font-size: 2rem;
		text-align: center;
		margin-bottom: 1.5rem;
		word-wrap: break-word;
		overflow-wrap: break-word;
		max-width: 100%;
	}

	.articles-title::after {
		left: 50%;
		transform: translateX(-50%);
		width: 80px;
	}

	.articles-subtitle {
		font-size: 1.05rem;
		text-align: center;
		margin-bottom: 2rem;
		word-wrap: break-word;
		overflow-wrap: break-word;
		max-width: 100%;
		line-height: 1.7;
	}

	.articles-wrapper {
		padding-top: 20px;
		padding-bottom: 40px;
		width: 100%;
		max-width: 100%;
		box-sizing: border-box;
	}

	.articles-article {
		padding: 20px 0 20px 25px;
		border-left: 2px solid #d5cec4;
		width: 100%;
		max-width: 100%;
		box-sizing: border-box;
		margin-bottom: 10px;
	}

	.articles-article::before {
		left: -7px;
		top: 25px;
		width: 10px;
		height: 10px;
	}

	.articles-article:not(:last-child)::after {
		margin-left: 25px;
		bottom: -5px;
	}

	/* Article content mobile */
	.articles-article h3,
	.articles-article .article-title {
		word-wrap: break-word !important;
		overflow-wrap: break-word !important;
		max-width: 100% !important;
		font-size: 1.3rem !important;
		line-height: 1.4 !important;
	}

	.articles-article p,
	.articles-article .article-description {
		word-wrap: break-word !important;
		overflow-wrap: break-word !important;
		max-width: 100% !important;
		line-height: 1.6 !important;
		font-size: 0.95rem !important;
	}

	.articles-article .article-date {
		font-size: 0.9rem !important;
		word-wrap: break-word !important;
		overflow-wrap: break-word !important;
	}
}

/* Small Mobile */
@media (max-width: 480px) {
	.articles-main-container {
		padding-top: 70px;
		padding-left: 16px;
		padding-right: 16px;
	}

	.articles-main-container::before {
		height: 150px;
		margin-left: -16px;
		margin-right: -16px;
	}

	.articles-title {
		font-size: 1.8rem;
		margin-bottom: 1.2rem;
	}

	.articles-subtitle {
		font-size: 1rem;
		margin-bottom: 1.8rem;
		line-height: 1.6;
	}

	.articles-article {
		padding: 18px 0 18px 20px;
	}

	.articles-article::before {
		left: -6px;
		top: 22px;
		width: 8px;
		height: 8px;
	}

	.articles-article:not(:last-child)::after {
		margin-left: 20px;
	}

	.articles-article h3,
	.articles-article .article-title {
		font-size: 1.2rem !important;
	}

	.articles-article p,
	.articles-article .article-description {
		font-size: 0.9rem !important;
	}
}

/* Extra Small Mobile */
@media (max-width: 360px) {
	.articles-main-container {
		padding-top: 65px;
		padding-left: 14px;
		padding-right: 14px;
	}

	.articles-main-container::before {
		margin-left: -14px;
		margin-right: -14px;
		height: 130px;
	}

	.articles-title {
		font-size: 1.6rem;
		margin-bottom: 1rem;
	}

	.articles-subtitle {
		font-size: 0.95rem;
		margin-bottom: 1.5rem;
	}

	.articles-article {
		padding: 16px 0 16px 18px;
	}

	.articles-article::before {
		left: -5px;
		top: 20px;
		width: 7px;
		height: 7px;
	}

	.articles-article:not(:last-child)::after {
		margin-left: 18px;
	}

	.articles-article h3,
	.articles-article .article-title {
		font-size: 1.1rem !important;
	}

	.articles-article p,
	.articles-article .article-description {
		font-size: 0.85rem !important;
	}
}

/* Tablet */
@media (min-width: 769px) and (max-width: 1024px) {
	.articles-main-container {
		padding-top: 100px;
		padding-left: 30px;
		padding-right: 30px;
	}
	
	.articles-title {
		font-size: 2.2rem;
		text-align: center;
	}

	.articles-title::after {
		left: 50%;
		transform: translateX(-50%);
	}
	
	.articles-subtitle {
		font-size: 1.15rem;
		text-align: center;
	}
	
	.articles-article {
		padding-left: 30px;
	}

	.articles-article:not(:last-child)::after {
		margin-left: 30px;
	}
}
